@tailwind base;
@tailwind components;
@tailwind utilities;
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

body {
  background-color: black;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes float {
  0%, 100% { 
    transform: translate(-50%, -10px) rotate(-360deg); 
  }
  50% { 
    transform: translate(-50%, 10px) rotate(-360deg); 
  }
}

.floating-cursor {
  height: 30px;
  position: absolute;
  top: 30%; 
  left: 50%;
  transform: translateX(-50%) rotate(-45deg);
  animation: float 2s ease-in-out infinite;
}

.floating-cursor img {
  height: 50px; 
}

/* Media query for mobile screens */
@media (max-width: 600px) {
  .floating-cursor {
    top: 20%; /* Adjust for mobile screens */
    /* Additional adjustments for mobile, if needed */
  }

  .floating-cursor img {
    height: 40px; /* Smaller size for mobile screens */
  }

  .view-work-button {
    margin-top: 10px !important;
  }

  .body-text {
    font-size: .98rem !important;
  }
}
  /* New media query for landscape orientation */
@media (max-width: 896px) and (orientation: landscape) {
  .floating-cursor {
    top: 9%; 
    left: 50%; 
    
  }
}

.name-title {
  transition: transform 0.3s ease, text-shadow 0.3s ease; /* Smooth transition for scaling and glowing */
}

.name-title:hover, .name-title.hover-effect {
  transform: scale(1.05); /* Enlarge on hover */
  text-shadow: 0 0 8px #fff; /* Glow effect on hover */
  cursor: pointer;
}

.body-text {
  font-size: 1rem !important;
}

.about-me-profile-image {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  box-sizing: border-box;
}

/* Media query for mobile screens */
@media (max-width: 600px) {
  .about-me-profile-image {
    width: 75%; /* Reduce image size on mobile */
  }
}

.paper {
  background-color: #1a1a1a; /* Dark background for each paper */
  color: white; /* White text for readability */
  padding: 20px;
  margin-bottom: 20px;
}

.paper h5 {
  font-weight: bold; /* Bold titles for positions */
}

.button {
  background-color: #61dafb; /* Stylish button color */
  color: white;
}

.button:hover {
  background-color: #49a6cc; /* Slightly darker on hover */
}
